import { useRouter } from 'next/navigation'
import React from 'react'

export default function Button(title) {

    const router = useRouter();

    const handleClick = () => {
        router.push(`/jobs/${title.toLowerCase().replace(/\s+/g, '-')}`);
    }
  return (
    <button onClick={handleClick} className='bg-slate-500 text-white px-4 py-2 rounded-md
    hover:bg-slate-950 transition-all duration-300 capitalize cursor-pointer'>
        Read More

    </button>
  )
}
